<template>
	<form @submit.prevent="$emit('submit')">
    <transport></transport>
    <reg-and-exp></reg-and-exp>
    <extra-info></extra-info>
      
    <div class="form--submit">
      <input :disabled="errorsLength > 0"
               class="form--send" 
               type="submit" 
               value="Рассчитать">
      <span class="form--error" v-if="errorsLength > 0">Данные заполнены некоректно</span>
    </div>
  </form>
</template>

<script>
	import Transport from "./CalcForm/Transport";
	import RegAndExp from "./CalcForm/RegAndExp";
	import ExtraInfo from "./CalcForm/ExtraInfo";
  import {mapGetters} from "vuex";

	export default {
    props: ["showCompaniesList"],

    computed: {
      ...mapGetters("errors",{
        "errorsLength": "errorsLength"}),
      ...mapGetters([
        "transportData"
        ])
    },
    
		components: {
			Transport,
			RegAndExp,
			ExtraInfo
		}
	}
</script>





