<template>
	<div>
		<fieldset class="form--block">
			<legend class="sub-header">Период страхования</legend>

			<otk v-if="showOtk"></otk>

			<label v-if="showOtk" for="period" class="sub2-header">Срок действия</label>
			<select :value="period"
			@change="changePeriod($event.target.value)"
			class="form--input"
			:class="periodError ? 'form--input__error' : false">
				<option :value="false"
						disabled
						style="display:none">
				</option>
				<option v-for="(option, key, index) in periodObj"
				v-if="option"
				id="period"
				:value="key">
				{{key}}
				</option>
			</select>

			<span class="form--error" v-if="periodError">Выберете период страхования</span>
		</fieldset>
	</div>
</template>

<script>
	import Otk from "./Otk/Otk.vue";
	import {mapGetters} from "vuex";
	import {mapActions} from "vuex";

	export default {
		data() {
			return {

			}
		},
		computed: {
			...mapGetters("errors",{
				"errors": "errors"
			}),
			...mapGetters("period",{
				period: "period"
			}),
			...mapGetters("place",{
				foreign: "foreign"
			}),
			...mapGetters("otk",{
				otk: "otk",
				otkTerm: "otkTerm"
			}),
			...mapGetters("transport",{
				transType: "transType",
        transExtraName: "transExtraName"
			}),
			...mapGetters("user",{
				userType: "userType"
			}),
			periodObj() {
				return {
					"15 дней": !this.transType === "Автобус" || this.foreign || this.otkTerm >= 15,
					"1 месяц": !this.transType === "Автобус"|| this.foreign || this.otkTerm >= 30,
					"2 месяца": !this.transType === "Автобус"|| this.foreign || this.otkTerm >= 60,
					"3 месяца": !this.transType === "Автобус"|| this.foreign || this.otkTerm >= 90,
					"4 месяца": !this.transType === "Автобус"|| this.foreign || this.otkTerm >= 120,
					"5 месяцев": !this.transType === "Автобус"|| this.foreign || this.otkTerm >= 150,
					"6 месяцев": !this.transType === "Автобус"|| !this.otk || this.otkTerm >= 180,
					"7 месяцев": !this.transType === "Автобус"|| !this.otk || this.otkTerm >= 210,
					"8 месяцев": !this.transType === "Автобус"|| !this.otk || this.otkTerm >= 240,
					"9 месяцев": !this.transType === "Автобус"|| !this.otk || this.otkTerm >= 270,
					"10 месяцев": !this.transType === "Автобус"|| !this.otk || this.otkTerm >= 300,
					"11 месяцев": !this.transType === "Автобус"|| !this.otk || this.otkTerm >= 330,
					"12 месяцев": !this.transType === "Автобус"|| !this.otk || this.otkTerm >= 360
				}
			},
			showOtk() {
				return (this.transType === "Легковой" && this.userType === "Юридическое лицо") ||
				this.transType === "Грузовик" ||
				this.transType === "Автобус" ||
				(this.transType === "Прицеп" && this.transExtraName === "E")
			},
			periodError() {
				if (this.otk) {
					if (this.errors.period && this.otkTerm !== null) {
						return true
					}
				}
				else {
					if (this.errors.period) {
						return true
					}
				}
			}
		},
		methods: {
			...mapActions("period", {
				changePeriod: "changePeriod"
			})
		},
		 components: {
		 	Otk
		 },
		 updated() {
		 	let trues = 0;
		 	for (let key in this.periodObj) {
		 		if(this.periodObj[key]) {
		 			trues++;
		 		}
		 	}
		 	if (!trues) {
		 		this.changePeriod(false);
		 	}
		 }
	}
</script>



